@import "../../../Styles/properties.scss";

.header {
  margin: 20px 0px 30px 0px;
  display: flex;
  justify-content: space-between;
  background: var(#{--components-background-default});
  padding: 5px 10px 5px 10px;
  border-radius: $border-radius;
  &-wrapper {
    display: flex;
    gap: 10px;
    align-items: center;
    cursor: pointer;
    .header-title {
      font-size: 30px;
      font-weight: bold;
      color: #4793ff;
    }
  }
}
